import React, { Component } from 'react';

//menu
import Navbar from './components/Navbar/Navbar';
import MobileNavMenu from './components/MobileNav/MobileNavMenu';
import Backdrop from './components/Backdrop/Backdrop';

//animate-on-scroll
import "./animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';

//fullscreen wrapper
import FullscreenBg from './components/FullscreenBg/FullscreenBg';

//pages
import Home from './components/Pages/Home';
import About from './components/Pages/About';
import Process from './components/Pages/Process';
import Journey from './components/Pages/Journey';
import Products from './components/Pages/Products';
import Contact from './components/Pages/Contact';

//background images
import HomeBg from './images/Background/home.jpg';
import AboutBg from './images/Background/about.jpg';
import ProcessBg from './images/Background/process.jpg';
import JourneyBg from './images/Background/journey.jpg';
import ProductBg from './images/Background/product.jpg';
import ContactBg from './images/Background/contact.jpg';

//background images placeholder for lazy loading
import HomePlaceholder from './images/Background/home-placeholder.jpg';
import AboutPlaceholder from './images/Background/about-placeholder.jpg';
import ProcessPlaceholder from './images/Background/process-placeholder.jpg';
import JourneyPlaceholder from './images/Background/journey-placeholder.jpg';
import ProductPlaceholder from './images/Background/product-placeholder.jpg';
import ContactPlaceholder from './images/Background/contact-placeholder.jpg';


class App extends Component {
  state = {
    mobileNavMenuOpen: false,
  };

  mobileNavToggleClickHandler = () => {
    this.setState((prevState) => {
      return {mobileNavMenuOpen: !prevState.mobileNavMenuOpen};
    });
  };

  backdropClickHandler = () => {
    this.setState({mobileNavMenuOpen: false});
  }

  render() {
    let backdrop;

    if (this.state.mobileNavMenuOpen) {
      backdrop = <Backdrop click={this.backdropClickHandler}
      />
    }
    return (
      <div className="App">
        {/* menu */}
        <Navbar
          mobileClickHandler={this.mobileNavToggleClickHandler}
          />
        <MobileNavMenu show={this.state.mobileNavMenuOpen}/>
        {backdrop}

        {/* main */}
        <FullscreenBg id='section0'src={HomeBg} placeholder={HomePlaceholder} >
          <Home/>
        </FullscreenBg>

        <FullscreenBg id='section1' src={AboutBg} placeholder={AboutPlaceholder}>
          <About />
        </FullscreenBg>

        <FullscreenBg id='section2' src={ProcessBg} placeholder={ProcessPlaceholder}>
            <Process/>
        </FullscreenBg>

        <FullscreenBg id='section3' src={JourneyBg} placeholder={JourneyPlaceholder}>
          <Journey/>
        </FullscreenBg>
        <FullscreenBg id='section4' src={ProductBg} placeholder={ProductPlaceholder}>
          <Products />
        </FullscreenBg>
        <FullscreenBg id='section5' src={ContactBg} placeholder={ContactPlaceholder}>
          <Contact />
        </FullscreenBg>
      </div>
    );
  }
}

export default App;
