import React from 'react';
import LazyLoad from 'react-lazy-load';
import './minibox.css';

const MiniBox = (props) => (
    <div className="minibox">
        <div className="image-box">
        <LazyLoad
                debounce={false}
                offsetVertical={500}
                throttle={250}
                >
                <img
                    src={props.img}
                    alt={props.alt}
                    style={{
                        width:`${props.width}`,
                        height:`${props.height}`
                        }}
                    >
            </img>
        </LazyLoad>
        </div>
        <div className="texta">
            {props.text}
        </div>
        <div className="textb">
            {props.text2}
        </div>
    </div>
);

export default MiniBox;
